import { Line } from "react-chartjs-2";

export const LineGraph = ({ chartData }) => {
  return (
    <div className="chart-div">
      <Line
        className="line-graph"
        data={chartData}
        height={500}
        width={600}
        options={{
          plugins: {
            title: {
              display: true,
              text: "Historical Chart (last 30 days)",
            },
            legend: {
              display: true,
              position: "bottom",
            },
            maintainAspectRatio: true,
            responsive: true,
          },
        }}
      />
    </div>
  );
};
