import "./App.css";
import Stats from "./components/Stats";
import SideBar from "./components/SideBar";
import Header from "./components/Header";
import Map from "./components/Map";
// import SortList from "./components/SortList";
import { sortData } from "./utilities";
import { useState, useEffect } from "react";
import { FormControl, Select, MenuItem, InputLabel } from "@mui/material";
import { LineGraph } from "./components/Chart";
import "leaflet/dist/leaflet.css";
import Numeral from "numeral";

function App() {
  const [countries, setCountries] = useState([]);
  const [country, setCountry] = useState("worldwide");
  const [countryData, setCountryData] = useState({});
  const [tableData, setTableData] = useState([]);
  const [mapCenter, setMapCenter] = useState({ lat: 51.505, lng: -0.09 });
  const [mapZoom, setMapZoom] = useState(3);
  const [mapCountries, setMapCountries] = useState([]);
  const [casesType, setCasesType] = useState("cases");
  const [chartData, setChartData] = useState({});

  // get some info from countries
  useEffect(async () => {
    const fetchData = async () => {
      await fetch("https://disease.sh/v3/covid-19/countries")
        .then((res) => res.json())
        .then((data) => {
          const countries = data.map((country) => ({
            name: country.country,
            iso: country.countryInfo.iso2,
            cases: country.cases,
            active: country.active,
          }));
          const sorted = sortData(data);
          setTableData(sorted); // side bar list
          setMapCountries(data);
          setCountries(countries);
        });
    };
    fetchData();
  }, []);

  // *ON COUNTRY CHANGE*
  // get the value of selected menu item
  const handleCountryChange = async (event) => {
    const countryID = event.target.value;
    // get data of country
    const url =
      countryID === "worldwide"
        ? "https://disease.sh/v3/covid-19/all"
        : `https://disease.sh/v3/covid-19/countries/${countryID}`;

    await fetch(url)
      .then((res) => res.json())
      .then((data) => {
        console.log(data.countryInfo);
        // display the name of selected item
        setCountry(countryID);
        // get ALL data
        setCountryData(data);
        // get coordinates to center map on selected country
        setMapCenter([data.countryInfo.lat, data.countryInfo.long]);
        setMapZoom(4);
      });
  };

  // get and set worldwide data (default)
  useEffect(async () => {
    await fetch("https://disease.sh/v3/covid-19/all")
      .then((res) => res.json())
      .then((data) => {
        setCountryData(data);
      });
  }, []);

  // fetch historical data for chart/graph
  useEffect(async () => {
    const historicalUrl =
      country === "worldwide"
        ? "https://disease.sh/v3/covid-19/historical/all?lastdays=30"
        : `https://disease.sh/v3/covid-19/historical/${country}?lastdays=30`;
    await fetch(historicalUrl)
      .then((res) => res.json())
      .then((data) => {
        let casesKeys = Object.keys(data.cases);
        let casesValue = Object.values(data.cases);
        let recoveredValue = Object.values(data.recovered);
        let deathsValue = Object.values(data.deaths);
        setChartData({
          labels: casesKeys.map((key) => key),
          datasets: [
            {
              label: "Cases",
              data: casesValue,
              borderColor: "red",
              fill: true,
              backgroundColor: "rgba(243, 11, 92, 0.3)",
            },
            {
              label: "Recovered",
              data: recoveredValue,
              borderColor: "rgba(75,192,192,1",
              fill: true,
              backgroundColor: "rgba(75,192,192,0.2)",
              hidden: true,
            },
            {
              label: "Deaths",
              data: deathsValue,
              borderColor: "purple",
              fill: true,
              backgroundColor: "rgba(143, 11, 92, 0.4)",
              hidden: true,
            },
          ],
        });
      });
  }, []);

  return (
    <div className="app">
      <Header />

      {/* DROPDOWN MENU */}
      <div className="dropdown-div">
        <FormControl className="header__dropdown">
          <InputLabel className="header__inputlabel" color="secondary">
            Select Country
          </InputLabel>
          <Select
            className="header__select"
            variant="outlined"
            value={country}
            onChange={handleCountryChange}
            color="secondary"
          >
            <MenuItem value={"worldwide"}>Worldwide</MenuItem>

            {countries.map((country) => (
              <MenuItem value={country.iso}>{country.name}</MenuItem>
            ))}
          </Select>
        </FormControl>
      </div>

      <div className="app__container">
        {/* LEFT SIDE */}
        <div className="app__left">
          <div className="stats__container">
            <Stats
              isRed
              active={casesType === "cases"}
              onClick={(e) => setCasesType("cases")}
              title="Confirmed Cases"
              today={countryData.todayCases}
              total={countryData.cases}
            />
            <Stats
              active={casesType === "recovered"}
              onClick={(e) => setCasesType("recovered")}
              title="Recovered"
              today={countryData.todayRecovered}
              total={countryData.recovered}
            />
            <Stats
              isRed
              active={casesType === "deaths"}
              onClick={(e) => setCasesType("deaths")}
              title="Deaths"
              today={countryData.todayDeaths}
              total={countryData.deaths}
            />
          </div>

          {/* MAP COMPONENT */}
          <Map
            casesType={casesType}
            mapCenter={mapCenter}
            mapZoom={mapZoom}
            mapCountries={mapCountries}
          />
        </div>
        {/* RIGHT SIDE */}
        <div className="app__right">
          <SideBar tableData={tableData} />
          <LineGraph chartData={chartData} />
        </div>
      </div>
    </div>
  );
}

export default App;
