import React from "react";
import { Card, CardContent } from "@mui/material";
import CountUp from "react-countup";
import "../Stats.css";

function Stats({ title, today, total, active, isRed, ...props }) {
  return (
    <div className="stats">
      <Card
        onClick={props.onClick}
        className={`card ${active && "card--selected"}`}
        elevation="6"
      >
        <CardContent className="card__content">
          <h2 className="card__title">{title}</h2>

          <h2 className={`card__today ${!isRed && "card--green"}`}>
            <span>+</span>
            <CountUp
              start={0}
              duration={1.5}
              end={`${today}`}
              separator=","
            ></CountUp>
            <span className="card__label">today</span>
          </h2>
          <h3 className="card__total">
            <CountUp
              start={0}
              duration={1.5}
              end={`${total}`}
              separator=","
            ></CountUp>
            <span className="card__label">total</span>
          </h3>
        </CardContent>
      </Card>
    </div>
  );
}

export default Stats;
