import React from "react";
import { Circle, Popup } from "react-leaflet";
import numeral from "numeral";

// const numeral = require("numeral");

const casesTypeColors = {
  cases: {
    hex: "#cc1034",
    rgb: "rgb(204,16,52)",
    multiplier: 6800,
  },
  recovered: {
    hex: "#7dd71d",
    rgb: "rgb(125,215,29)",
    multiplier: 7200,
  },
  deaths: {
    hex: "#fb4443",
    rgb: "rgb(251, 68, 67)",
    multiplier: 8000,
  },
};

// Sort sidebar list by Most Cases
export const sortData = (data) => {
  const sorted = [...data];

  return sorted.sort((a, b) => (a.active > b.active ? -1 : 1));
};

// display data on map with circles
export const showDataOnMap = (data, casesType = "cases") =>
  data.map((country) => (
    <Circle
      center={[country.countryInfo.lat, country.countryInfo.long]}
      radius={Math.sqrt(
        country[casesType] * casesTypeColors[casesType].multiplier
      )}
      color={casesTypeColors[casesType].hex}
      fillColor={casesTypeColors[casesType].hex}
      fillOpacity={0.2}
    >
      <Popup>
        <div className="popup-container">
          <div
            className="popup-flag"
            style={{ backgroundImage: `url(${country.countryInfo.flag})` }}
          ></div>
          <div className="popup-name">{country.country}</div>
          <div className="popup-data">
            Cases: {numeral(country.cases).format("0,0")}
          </div>
          <div className="popup-data">
            Recovered: {numeral(country.recovered).format("0,0")}
          </div>
          <div className="popup-data">
            Deaths: {numeral(country.deaths).format("0,0")}
          </div>
        </div>
      </Popup>
    </Circle>
  ));

